
#container{padding:1rem 1.2rem;margin:0 auto;height:80%;text-align:center
}
#container .search-input{margin:0 auto;padding:.8rem 1.2rem;width:80%;font-size:1.2rem;color:#000;border:none;border-radius:5px;line-height:1rem
}
#container .search-input:active,#container .search-input:focus{outline:none
}
#container .info{color:#fff;letter-spacing:.05em;opacity:.8
}
#container .trans-button{padding:.6rem 1.2rem;background-color:#fff;border:none;font-size:1.2rem;line-height:1.2rem;font-family:cursive,"Microsoft Yahei",sans-serif;border-radius:5px
}


