
#add .addForm{padding:.4rem 0;height:100%;opacity:1;transition:opacity ease-out 1s
}
#add .addForm .item{padding:.4rem .8rem;margin:.8rem 0;background-color:#fff;font-size:1.2rem
}
#add .addForm .item .item-label{display:inline-table;width:35%;vertical-align:top
}
#add .addForm .item .item-input{display:inline-table;width:65%
}
#add .addForm .item .item-input input[type="text"]{padding:.25rem;height:100%;border:none;font-family:cursive,"Microsoft Yahei",sans-serif;font-size:1rem;line-height:1.2rem
}
#add .addForm .item .item-input input[type="text"]:focus,#add .addForm .item .item-input input[type="text"]:active{outline:none
}
#add .addForm .item .item-input textarea{width:100%;font-family:cursive,"Microsoft Yahei",sans-serif;font-size:1rem;line-height:1.2rem;border:none;resize:none
}
#add .addForm .item .item-input textarea:focus,#add .addForm .item .item-input textarea:active{outline:none
}
#add .addForm .control-item{margin-top:5rem;width:100%
}
#add .addForm .control-item .btn{width:50%;padding:1rem 0;text-align:center;border:none
}
#add .addForm .control-item .btn.add{color:#000;background-color:#eee
}
#add .addForm .control-item .btn.cancel{color:#000;background-color:#fff
}
#add .addForm.complate{opacity:0
}


